/* -- Reset -- */
* {
	box-sizing: border-box;
}

body, h1, h2, h3, h4, h5, h6, p, div, table, tr, td, caption {
	margin: 0;
	padding: 0;
}

body {
	font-size: 16px;
}

p {
	font-size: 1em;
}

pre {
	font-size: 0.9em;
}

h1 {
	font-size: 2em;
}

h2 {
	font-size: 1.6em;
}

h3 {
	font-size: 1.4em;
}

h4 {
	font-size: 1.2em;
}

h5 {
	font-size: 1em;
}

h6 {
	font-size: 0.8em;
}

/* -- Base styles -- */
html,
body {
	height: 100%;
}

body {
	font-family: FreeSerif, "Times New Roman", Times, serif;
	color: #333333;
	background-color: #333333;
}

p,
address {
	margin-bottom: 1rem;
}

p,
ul li,
ol li,
dl dt,
dl dd,
blockquote {
	font-size: 1em;
	line-height: 1.29em;
}

blockquote {
	background-color: #ededed;
	margin: 20px !important;
	padding: 1em;
	font-style: italic;
	clear: both;
}

blockquote p {
	font-size: 1em;
}

h1 {
	font-size: 2.14em;
}

h2 {
	font-size: 1.71em;
}

h3 {
	font-size: 1.29em;
}

h4 {
	font-size: 1em;
}

h5 {
	font-size: 0.875em;
}

h6 {
	font-size: 0.75em;
}

a:link,
a:visited {
	color: #3131c4;
	text-decoration: none;
}

a:hover,
a:active {
	color: #c03030;
	text-decoration: underline;
}

img {
	max-width: 100%;
	height: auto;
}

a img {
	border: 0;
}

video {
	width: 100% !important;
	height: auto !important;
}

table {
	margin: 0 auto;
}

table,
table th,
table td {
	border: 1px solid #ededed;
}

table th, table td {
	padding: 0.5em;
}

table caption {
	color: #f5f5f5;
	background-color: #000000;
	font-weight: bold;
}

pre,
code,
kbd {
	font-family: "Courier New", Courier, monospace;
}

pre {
	background-color: #ededed;
	border: 1px solid #c0c0c0;
	overflow-x: auto;
	padding: 1em;
}

form label {
	margin-right: 6px;
}

button,
form input,
form textarea,
select {
	color: #333333;
	border: 1px solid #c0c0c0;
	padding: 2px 6px;
	font-size: 1em;
}

form input:focus,
form textarea:focus {
	background-color: #ededed;
}

button,
form input,
form textarea,
select,
select option {
	font-family: FreeSerif, "Times New Roman", Times, serif;
}

button,
.wp-block-button__link,
form input[type="reset"],
form input[type="submit"],
select {
	background-color: #ededed;
	margin: 6px;
	min-width: 72px;
}

select option {
	height: 36px;
}

form textarea {
	width: 100%;
}

form input#searchsubmit {
	border: 0;
}

fieldset {
	border: 1px solid #c0c0c0;
	margin-bottom: 1em;
}

hr {
	border-bottom: 2px solid rgba(0, 0, 0, 0.25);
	border-top: transparent;
	margin: 1em 0;
}

/* -- Layout styles -- */
/* Wrapper */
#wrapper {
	min-width: 320px;
	max-width: 1366px;
	width: 100%;
}

.blog-wide #wrapper {
	max-width: 1920px;
}

@media screen and (min-width: 40.063em) {
	#wrapper {
		display: flex;
		flex-direction: column;
		margin: 0 auto;
		min-height: 100%;
		width: 90%;
	}
}

/* Header and Access bar */
#header {
	position: relative;
	width: 100%;
}

#access {
	width: 100%;
	float: left;
	border-top: 4px solid #333333;
	background-color: #808080;
}

@media screen and (min-width: 40.063em) {
	.page-template-thin-header #access {
		position: static;
	}
}

@media screen and (min-width: 48em) {
	#access {
		height: 36px;
		position: static;
		border-bottom: 1px solid #333333;
		border-top: transparent;
	}
	.page-template-full-width #access,
	.page-template-full-page-width #access,
	.woocommerce #access {
		border-bottom: none;
		height: 0;
	}
}

/* Main */
#main {
	display: grid;
	flex-grow: 1;
	grid-template-columns: minmax(320px, auto);
	width: 100%;
}

#main.texture {
	background: url(images/noise-texture.png) #ededed;
}

.no-decs #main,
.no-decs.two-col-l #main,
.no-decs.three-col-l #main {
	border: transparent;
}

.page-template-landing #wrapper {
	max-width: none;
	width: 100%;
}

.page-template-landing #main {
	background-image: none;
	border-left: none;
	border-right: none;
}

@media screen and (min-width: 64.063em) {
	#main {
		border-left: 4px solid rgba(0, 0, 0, 0.25);
		grid-template-columns: minmax(0, auto) 284px;
	}
	.page-template-landing #main,
	.page-template-thin-header #main,
	.page-template-full-page-width #main,
	.page-template-full-page-width.two-col-l #main,
	.page-template-full-page-width.three-col-r #main,
	.page-template-full-page-width.three-col-l #main,
	.page-template-page_featured_categories #main {
		grid-template-columns: 100%;
	}
	.page-template-full-page-width #main,
	.page-template-full-page-width.two-col-l #main,
	.page-template-full-page-width.three-col-l #main {
		border: transparent;
	}
	.page-template-thin-header #main,
	.page-template-page_featured_categories #main {
		border-left: 4px solid rgba(0, 0, 0, 0.25);
		border-right: 4px solid rgba(0, 0, 0, 0.25);
	}
	.three-col-r #main {
		grid-template-columns: minmax(0, auto) 404px;
	}
	.two-col-l #main {
		border-left: transparent;
		border-right: 4px solid rgba(0, 0, 0, 0.25);
		grid-template-columns: 284px minmax(0, auto);
	}
	.three-col-l #main {
		border-right: 4px solid rgba(0, 0, 0, 0.25);
		grid-template-columns: 400px minmax(0, auto);
	}
	.page-template-full-width #main,
	.page-template-full-width.three-col-r #main {
		border-right: 4px solid rgba(0, 0, 0, 0.25);
	}
	.page-template-full-width.two-col-l #main,
	.page-template-full-width.three-col-l #main {
		border-left: 4px solid rgba(0, 0, 0, 0.25);
	}
	.page-template-full-width #container,
	.page-template-full-width.two-col-l #container,
	.page-template-full-width.three-col-l #container {
		grid-column: 1 / 3;
	}
	.page-template-full-page-width.two-col-l #container,
	.page-template-full-page-width.three-col-l #container {
		grid-column: 1 / 2;
	}
}

/* Container and Content */
#container {
	grid-column: 1 / 2;
	width: 100%;
}

@media screen and (min-width: 64.063em) {
	.two-col-l #container {
		grid-column: 2 / 3;
	}
	.three-col-l #container {
		grid-column: 2 / 3;
	}
	.page-template-landing #container,
	.page-template-thin-header #container,
	.page-template-page_featured_categories #container {
		grid-column: 1 / 3;
	}
	.woo-content-container {
		grid-column: 1 / 2;
	}
	.two-col-l .woo-content-container,
	.three-col-l .woo-content-container {
		grid-column: 2 / 3;
	}
}

.mag-grid #content {
	padding: 20px;
}

/* Sidebar */
.no-decs #sidebar {
	border: transparent;
}

@media screen and (min-width: 64.063em) {
	#sidebar {
		border-left: 4px solid rgba(0, 0, 0, 0.25);
		border-right: 4px solid rgba(0, 0, 0, 0.25);
		grid-column: 2 / 3;
	}
	.three-col-r #sidebar {
		display: grid;
		grid-template-columns: 196px 200px;
	}
	.two-col-l #sidebar {
		grid-column: 1 / 2;
		grid-row: 1 / 1;
	}
	.three-col-l #sidebar {
		border-left: transparent;
		display: grid;
		grid-column: 1 / 2;
		grid-row: 1 / 1;
		grid-template-columns: 196px 200px;
	}
	.no-decs #sidebar {
		border-left: 1px solid rgba(0, 0, 0, 0.25);
	}
	.two-col-l.no-decs #sidebar,
	.three-col-l.no-decs #sidebar {
		border-left: transparent;
		border-right: 1px solid rgba(0, 0, 0, 0.25);
	}
	.woocommerce .sidebar {
		grid-row-start: 1;
	}
}

/* Comments */
#comments-area {
	border-top: 2px solid #ededed;
	padding-top: 1em;
}

#respond {
	background-color: #ffffff;
	padding: 1em;
	color: #333333;
	font-weight: normal;
	border-top: 2px solid #ededed;
	clear: both;
}

/* Footer */
#footer {
	background-color: #000000;
	clear: both;
	float: left;
	min-height: 50px;
	padding: 20px 2.5%;
	position: relative;
	width: 100%;
}

/* -- Module styles -- */
/* Branding */
.branding {
	background-color: #000000;
	float: left;
	width: 100%;
}

/* Site Title Block */
.site-title-block {
	padding: 20px 0px;
}

.custom-header .site-title-block {
	background-color: rgba(0, 0, 0, 0.5);
	border-bottom: 2px solid rgba(255, 255, 255, 0.25);
}

@media screen and (min-width: 40.063em) {
	.site-title-block {
		width: 75%;
	}
}

@media screen and (min-width: 64.063em) {
	.site-title-block {
		width: 50%;
	}
}

/* Site Title and Description */
.blog-title {
	font-size: 2.14em;
	padding: 0px 40px;
}

.blog-title a:link,
.blog-title a:visited {
	color: #ffffff;
	text-decoration: none;
}

.wp-custom-logo .blog-title {
	font-size: 1.71em;
}

.blog-title-landing {
	background-color: transparent;
	border-bottom: 1px solid rgba(0, 0, 0, 0.25);
	font-size: 1.29em;
	padding: 6px 40px;
	text-align: center;
	width: auto;
}

@media screen and (min-width: 40.063em) {
	.blog-title-landing {
		border-bottom: transparent;
		border-right: 1px solid rgba(0, 0, 0, 0.25);
		float: left;
		height: 36px;
		padding: 3px 2.5% 0 2.5%;
	}
}

.subtitle {
	color: #ffffff;
	padding: 0px 40px;
}

.wp-custom-logo .subtitle {
	font-size: 1em;
}

@media screen and (min-width: 48em) {
	.wp-custom-logo .subtitle {
		font-size: 1.29em;
	}
}

/* Custom Logo */
.custom-logo-link img {
	max-height: 100px;
	width: auto;
	float: left;
}

@media screen and (min-width: 64.063em) {
	.custom-logo-link img {
		margin: 20px;
		max-height: 200px;
	}
}

/* Main Menu */
.menubar {
	list-style: none;
	margin: 0;
	padding: 0;
}

.menubar-pages-list-item, .menubar-cats-list-item {
	border-bottom: 1px solid rgba(0, 0, 0, 0.25);
	list-style: none;
}

.menubar-title,
.menubar .button-dropdown {
	display: inline-block;
}

.menubar-title {
	color: #ffffff;
	cursor: default;
	font-size: 1em;
	padding: 10px 20px;
}

.menubar .button-dropdown {
	margin: 9px;
	float: right;
}

@media screen and (min-width: 48em) {
	.menubar {
		float: left;
		height: 40px;
		position: relative;
	}
	.menubar-title {
		padding: 7.5px 20px;
	}
	.menubar-pages-list-item, .menubar-cats-list-item {
		border-bottom: transparent;
		border-right: 1px solid rgba(0, 0, 0, 0.25);
		float: left;
		min-width: 110px;
		padding: 0px;
	}
}

.pagemenu .menu {
	background-color: #000000;
	border: 10px solid #ededed;
	padding: 10px;
}

.pagemenu-vertical {
	float: none;
	max-height: 2500px;
	overflow-y: hidden;
	transition: all 1s ease-in-out;
	z-index: 2;
}

.pagemenu-vertical .menu {
	float: none;
	height: auto;
}

.pagemenu-vertical .page_item,
.pagemenu-vertical .cat-item,
.pagemenu-vertical .menu-item {
	border: 0;
	float: none;
	list-style: none;
}

.pagemenu-vertical .page_item a,
.pagemenu-vertical .cat-item a,
.pagemenu-vertical .menu-item a {
	display: block;
	padding: 0.5em;
}

.pagemenu-vertical .page_item a:link, .pagemenu-vertical .page_item a:visited,
.pagemenu-vertical .cat-item a:link,
.pagemenu-vertical .cat-item a:visited,
.pagemenu-vertical .menu-item a:link,
.pagemenu-vertical .menu-item a:visited {
	color: #ffffff;
	text-decoration: none;
}

.pagemenu-vertical .page_item a:hover, .pagemenu-vertical .page_item a:active,
.pagemenu-vertical .cat-item a:hover,
.pagemenu-vertical .cat-item a:active,
.pagemenu-vertical .menu-item a:hover,
.pagemenu-vertical .menu-item a:active {
	background-color: #ededed;
	color: #000000;
}

.pagemenu-vertical .children,
.pagemenu-vertical .sub-menu {
	padding-left: 0px;
}

.pagemenu-vertical .children .page_item,
.pagemenu-vertical .children .cat-item,
.pagemenu-vertical .sub-menu .menu-item {
	margin-left: 10px;
}

@media screen and (min-width: 48em) {
	.pagemenu-vertical {
		margin-top: 0;
		position: absolute;
		top: 36px;
	}
	.pagemenu-vertical .page_item,
	.pagemenu-vertical .cat-item,
	.pagemenu-vertical .menu-item {
		width: 200px;
	}
	.pagemenu-vertical .children .page_item,
	.pagemenu-vertical .children .cat-item,
	.pagemenu-vertical .sub-menu .menu-item {
		width: auto;
	}
}

/* Header Search Bar */
.searchbar {
	border-bottom: 1px solid rgba(0, 0, 0, 0.25);
	height: 43px;
}

.searchbar .search-form {
	height: 42px;
	margin: 0 auto;
	width: 200px;
}

.searchbar label {
	font-size: 1em;
	font-weight: bold;
	color: #ffffff;
	margin-left: 1em;
}

.searchbar .search-input,
.searchbar .search-submit {
	display: inline-block;
	margin: 3px 0px;
	vertical-align: bottom;
}

.searchbar .search-input {
	min-height: 36px;
	border: 1px solid #333333;
	width: 120px;
}

.searchbar .search-submit {
	margin: 9px 0.5em;
}

@media screen and (min-width: 40.063em) {
	.searchbar,
	.searchbar .searchform {
		height: 36px;
	}
	.searchbar .search-input {
		margin: 6px 0px;
		min-height: auto;
	}
	.searchbar .search-submit {
		margin: 5px 0.5em;
	}
}

@media screen and (min-width: 48em) {
	.searchbar {
		border-left: 1px solid rgba(0, 0, 0, 0.25);
		border-bottom: transparent;
		position: absolute;
		right: 0px;
		top: auto;
		width: 200px;
	}
	.searchbar .search-input {
		margin-left: 1em;
	}
}

@media screen and (min-width: 64.063em) {
	.searchbar {
		width: 280px;
	}
	.three-col-r .searchbar,
	.three-col-l .searchbar {
		width: 200px;
	}
}

/* Global Search Bar styles */
.search-input {
	color: #808080;
	display: inline-block;
}

.search-submit {
	background: url(images/jgd-bizelite-assets.svg) no-repeat -204px -382px;
	border: transparent;
	display: inline-block;
	height: 24px;
	margin: 0px 0.5em;
	min-width: 24px;
	padding: 0px;
	vertical-align: bottom;
	width: 24px;
}

/* Social Profiles Menu */
.menu-social {
	border-bottom: 1px solid rgba(0, 0, 0, 0.25);
	height: 36px;
	text-align: center;
	width: auto;
}

.menu-social .menu-items {
	list-style-type: none;
	margin: 0px;
	max-height: 36px;
	padding: 0px;
	transition: 0.5s;
}

.menu-social .menu-item {
	display: inline-block;
	vertical-align: middle;
}

.menu-social .button-dropdown {
	margin: 9px;
	position: absolute;
	right: 0px;
	top: 0px;
}

.menu-social a:link,
.menu-social a:visited {
	color: #ffffff;
	display: block;
	height: 36px;
	padding: 0.5em;
	text-decoration: none;
	width: 36px;
}

@media screen and (min-width: 48em) {
	.menu-social {
		border-left: transparent;
		border-bottom: transparent;
		position: absolute;
		right: 0px;
		top: 0px;
		width: 200px;
	}
	.menu-social .social-panel-expand {
		background-color: #808080;
		border-bottom: 4px solid #333333;
		max-height: 72px;
	}
}

@media screen and (min-width: 64.063em) {
	.menu-social {
		width: 280px;
	}
}

/* Content Navigation */
.navigation {
	float: left;
	font-weight: bold;
	padding: 20px 5%;
	width: 100%;
}

.navigation .navlinks,
.navigation .nav-links {
	text-align: center;
	margin: 0 auto;
}

.navigation .nav-links {
	min-height: 72px;
}

.navigation .nav-links a {
	text-decoration: none;
}

.navigation .nav-separator {
	background: url(images/jgd-bizelite-assets.svg) no-repeat -20px -45px;
	display: inline-block;
	height: 20px;
	width: 20px;
}

.navigation .nav-previous,
.navigation .nav-next {
	margin: 1em 0;
}

@media screen and (min-width: 40.063em) {
	.navigation .nav-links {
		min-height: 20px;
	}
	.navigation .nav-previous,
	.navigation .nav-next {
		margin: 0px;
	}
	.navigation .nav-previous {
		float: left;
		max-width: 45%;
	}
	.navigation .nav-next {
		float: right;
		max-width: 45%;
	}
}

@media screen and (min-width: 85.438em) {
	.navigation {
		padding: 20px 10%;
	}
}

/* -- Posts / Pages -- */
.entry-title a:link,
.entry-title a:visited {
	color: #000000;
	text-decoration: none;
}

.entry-title a:active,
.entry-title a:hover {
	color: #808080;
}

.post .index-meta,
.type-page .index-meta {
	padding: 20px 0px;
}

.post .index-meta p,
.type-page .index-meta p {
	border-bottom: 1px solid #c0c0c0;
	margin-bottom: 0px;
	padding: 0.325em 0;
}

.post-blog,
.type-page,
.error404 {
	clear: both;
	padding: 0.5em 0px;
}

.post-blog .entry,
.type-page .entry,
.error404 .entry {
	background-color: #ffffff;
	border: 2px solid #c0c0c0;
	clear: both;
	margin: 20px 5%;
	overflow-x: hidden;
	padding: 20px 1.5em;
}

.post-blog .entry-title,
.type-page .entry-title,
.error404 .entry-title {
	line-height: 1em;
	padding: 0px 5%;
}

.post-blog .entry > *,
.type-page .entry > *,
.error404 .entry > * {
	margin-left: auto;
	margin-right: auto;
	max-width: 740px;
}

.post-blog .entry h1,
.post-blog .entry h2,
.post-blog .entry h3,
.post-blog .entry h4,
.post-blog .entry h5,
.post-blog .entry h6,
.type-page .entry h1,
.type-page .entry h2,
.type-page .entry h3,
.type-page .entry h4,
.type-page .entry h5,
.type-page .entry h6,
.error404 .entry h1,
.error404 .entry h2,
.error404 .entry h3,
.error404 .entry h4,
.error404 .entry h5,
.error404 .entry h6 {
	margin: 0.2em auto;
}

.post-blog .index-meta,
.type-page .index-meta,
.error404 .index-meta {
	padding: 20px 5%;
}

.post-blog .index-meta span[class*="ti-"],
.type-page .index-meta span[class*="ti-"],
.error404 .index-meta span[class*="ti-"] {
	margin: 0px 3px;
}

.post-blog .index-meta span[class="ti-pencil"],
.type-page .index-meta span[class="ti-pencil"],
.error404 .index-meta span[class="ti-pencil"] {
	float: left;
	margin: 20px 10px 0px 0px;
}

.post-blog .index-meta span[class="ti-pencil"] + div,
.type-page .index-meta span[class="ti-pencil"] + div,
.error404 .index-meta span[class="ti-pencil"] + div {
	margin-left: 20px;
}

.post-blog .page-links,
.type-page .page-links,
.error404 .page-links {
